import {useState, useEffect} from 'react';
import { Link } from 'react-router-dom';
import useMarvelService from '../../services/MarvelService';
import setContent from '../../utils/setContent';
import './comicsList.scss';

const ComicsList = () => {
    const [data, setData] = useState([]);
    const [newItemLoading, setNewItemLoading] = useState(false);
    const [offset, setOffset] = useState(0);
    const [comicsEnded, setComicsEnded] = useState(false);

    const {process, setProcess, getAllComics} = useMarvelService();

    const onComicsListLoaded = (newData) => {
        let ended = false;
        if (newData.length < 8) ended = true;

        setData(data => [...data, ...newData]);
        setNewItemLoading(false);
        setOffset(offset => (offset + 8));
        setComicsEnded(ended);
    }

    const loadComicsList = (offset, initial) => {
        initial ? setNewItemLoading(false) : setNewItemLoading(true);
        initial ? setProcess('loading') : setProcess('confirmed');
        getAllComics(offset)
            .then(onComicsListLoaded)
            .then(() => setProcess('confirmed'));
    }

    useEffect(() => {
        loadComicsList(offset, true);
        //eslint-disable-next-line
    }, [])

    const renderComics = (comics) => {
        const elements = comics.map((item, i) => {
            return (
                <li className="comics__item"
                    tabIndex={0}
                    key={i}>
                    <Link to={`/comics/${item.id}`}>
                        <img src={item.thumbnail} alt={item.title} className="comics__item-img"/>
                        <div className="comics__item-name">{item.title}</div>
                        <div className="comics__item-price">{item.price}</div>
                    </Link>
                </li>
            )
        });
        return (
            <ul className="comics__grid">
                {elements}
            </ul>
        )
    }

    return (
        <div className="comics__list">
            {setContent(process, () => renderComics(data))}
            <button className="button button__main button__long"
                disabled={newItemLoading}
                style={{display: comicsEnded ? "none" : 'block'}}
                onClick={() => loadComicsList(offset)}>
                <div className="inner">load more</div>
            </button>
        </div>
    )
}

export default ComicsList;